.main-wrapper {
    position: fixed;
    top: 10%;
    right: 100px;
    transform: translateY(-50%);
    z-index: 4;
    top:50%;
  }
  
  .dot-menu {
    list-style-type: none;
    font-size: 1.5em;
    font-family: Oswald;
    font-weight: 400;
    line-height: 1.75;
    text-align: right;
    padding-right: 1em;
    width: 200px;
    
    
  }
  .dot-menu li {
    letter-spacing: 0.1em;
    cursor: pointer;
    transition: all 1s;
    margin-left: 20px;
    
  }
  .dot-menu li a {
    position: relative;
    text-decoration: none;
    color: #ffffff;
    
  }

  .dot-menu li a:before {
    content: "";
    width: 15px;
    height: 5px;
    background-color: #7f8c8d;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: -300px;
    transform: translate-y(-50%);
    opacity: 0;
    transition: opacity 0.6s ease, left 1s ease, width 1s ease;
  }
  .dot-menu li:hover {
    letter-spacing: 0.5em;
    color: #5a6465;
  }
